class CallModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showWarning: true };
    this.handleToggleClick = this.handleToggleClick.bind(this);
  }
  handleToggleClick() {
    this.setState(state => ({
      showWarning: !state.showWarning
    }));
  }
  render() {
    return (
      <span onClick={this.handleToggleClick}>
        {this.state.showWarning ?
        <span  className="fixed-action-btn">
        <a className="btn-floating btn-large red">
          <i style={{fontSize:"2.3rem", paddingLeft: "21%"}} className="large material-icons">local_phone</i>
        </a>
      </span >:
          <Modal
            className='center-align'
            bottomSheet={false}
            fixedFooter={false}
            header="CONTACT"
            id="modal1"
            open={true}
            options={{
              dismissible: true,
              endingTop: '10%',
              inDuration: 250,
              onCloseEnd: null,
              onCloseStart: null,
              onOpenEnd: null,
              onOpenStart: null,
              opacity: 0.5,
              outDuration: 250,
              preventScrolling: false,
              startingTop: '4%'
            }} >
            <div class="modal-content">
              <Row>
                <Col s={12}>
                  <a className="btn btn-block orange" large href="tel://+14356551446">(435)655-1446<Icon left>local_phone</Icon></a>
                </Col>
              </Row><br/>
              <Row>
                <Col s={12}>
                  <a className="btn btn-block orange" large href="mailTo://info@utahcountertops.com">info@utahcountertops.com<Icon left>mail</Icon></a>
                </Col>
              </Row>
              <Map />
            </div>
            <br />
          </Modal>
        }
      </span>

    );
  }
}


function NoMatch() {
  let location = useLocation();

  return (
    <div>
      <h3>
        No match for <code>{location.pathname}</code>
      </h3>
    </div>
  );
}
export default function App() {
  return (
    <BrowserRouter >
      <main>

        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route exact path="/review">
            <Review />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/gallery">
            <Gallery />
          </Route>
          <Route path="/pricing">
            <Full />
          </Route>
          <Route path="/residential">
            <Residential/>
          </Route>
          <Route path="/commercial">
            <Commercial />
          </Route>

          <Route path="*">
            <NoMatch />
          </Route>
        </Switch>
      </main>
      <CallModal />
      <br />
      <Foot />
    </BrowserRouter>
  );
}


