import React from 'react';
import { Modal, Divider} from 'react-materialize';
// import {Link, useLocation } from 'react-router-dom';
import logo from '../media/img/logo.png';
import Map from './Map'



export default class ContactModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showWarning: true };
    this.handleToggleClick = this.handleToggleClick.bind(this);
  }

  handleToggleClick() {
    this.setState(state => ({
      showWarning: !state.showWarning
    }));
  }
  render() {
    return (
      <div onClick={this.handleToggleClick}>
        {this.state.showWarning ?
          <span>Contact</span>
          : <Modal
            className='center-align higher'
            bottomSheet={false}
            fixedFooter={false}
            header="CONTACT"
            id="modal1"
            open={true}
            options={{
              dismissible: false,
              endingTop: '10%',
              inDuration: 250,
              onCloseEnd: null,
              onCloseStart: null,
              onOpenEnd: null,
              onOpenStart: null,
              opacity: 0.5,
              outDuration: 250,
              preventScrolling: true,
              startingTop: '4%'
            }} >
            <div class="divider" /><br />
            <div class="modal-content">
              <br />
              <a className="" href="tel://+14356551446" waves="light">
                <i className="material-icons lrg">local_phone</i>
              </a><h6> (435) 655-1446</h6>
              <br />
              <a className="btn btn-block orange text-white" href="mailTo://info@utahcountertops.com" waves="light">
                <i className="material-icons">mail</i></a><h6>  info@utahcountertops.com</h6></div>

            <Map />
            <br />
            <Divider />
          </Modal>
        }
      </div>

    );
  }
}

