class Med extends React.Component {
  render() {
    const listItems = l.map((l) =>
      <Col s={12} m={l.sz} className="no-pad gallery-container">
        <MediaBox
          id="MediaBox_7"
          options={{
            inDuration: 275,
            onCloseEnd: null,
            onCloseStart: null,
            onOpenEnd: null,
            onOpenStart: null,
            outDuration: 200
          }}
        >
          <img
            alt=""
            src={l.img}

          />
        </MediaBox>
      </Col>
      // <span>
      //     <img className="carousel" src= />
      //     <span className='llegend'>{l.txt}</span>
      // </span>
    );
    return (
      <Container >
        <h4 className="grey-text">GALLERY</h4>

        <Row>
          {listItems}
        </Row>

      </Container>
    );
  }
}


